.error-page{
    min-height: 100vh;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    text-align: center;
}
.error-page header,
.error-page section{
    height: 100%;
}
.error-page header h1{
    font-size: 25vw;
    margin-bottom: 50px;
    background: linear-gradient(to right, red, blue);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    background-size: 200%;
    animation: move 2s ease-in infinite alternate;
}
@keyframes move {
    from{
        background-position: 0% 0%;
    }
    to{
        background-position: 100% 100%;
    }
}
.error-page section h2{
    margin-bottom: 20px;
}
.error-page section p{
    width: 100%;
    max-width: 450px;
    margin-bottom: 20px;
}
.error-page section a{
    text-decoration: none;
    padding: 10px 20px;
    border-radius: 15px;
    background-color: rgb(26, 75, 220);
    color: white;
    outline: none;
    border: none;
    box-shadow: 2px 2px 4px royalblue;
    cursor: pointer;
}