.form{
    --bg: rgb(40,49,60);
    width: 100%;
    max-width: 600px;
    margin: 50px auto;
    background-color: var(--bg);
    border-radius: 10px;
    padding: 30px;
    box-shadow: 2px 2px 10px gray;
    color: white;
}
.form h3{
    text-align: center;
    margin-bottom: 20px;
    color: white;
    background: rgb(1, 152, 122);
    padding: 10px;
    display: none;
}
.form h3.duplicate{
    color: #b00000;
    background: #fa9191;
}
.form h1{
    text-align: center;
    color: rgb(0, 255, 204);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 2.4rem;
}
.form .input,
.form .radio {
    margin-top: 35px;
}
.form .input input::placeholder{
    color: rgb(183, 183, 183);
}
.form .input select,
.form .input input,
.form .radio label,
.form .input button{
    width: 100%;
    padding: 10px;
    outline: none;
    background-color: transparent;
    border: 2px solid white;
    border-radius: 10px;
    color: white;
    font-size: 1.1rem;
}
.form .input button{
    background-color: white;
    color: var(--bg);
    font-size: 1.2rem;
    cursor: pointer;
}
.form .input select:focus,
.form .input input:focus,
.form .radio label:focus,
.form .input button:focus{
    box-shadow: 0px 0px 10px white;
}
.form .radio{
    display: flex;
    justify-content: flex-start;
    gap: 40px;
    align-items: center;
}
.form .radio input{
    display: none;
}
.form .radio label{
    padding: 10px 20px;
    cursor: pointer;
}
.form .radio input:checked ~ label{
    background-color: white;
    color: var(--bg);
}