.top-nav{
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: var(--green);
    padding: 10px 20px;
    box-shadow: 2px 2px 10px gray;
    border-bottom: 2px solid green;
}
.top-nav a{
    padding: 10px 20px;
    display: inline-block;
    text-decoration: none;
    color: white;
    margin-right: 20px;
    transition: 0.4s all;
    font-size: 1.1rem;
}
.top-nav a:hover{
    background-color: white;
    color: var(--green);
}
.top-nav a.active{
    background-color: white;
    color: var(--green);
}