.table{
    width: 100%;
    max-width: 800px;
    margin: 50px auto;
    box-shadow: 2px 2px 15px rgb(202, 202, 202);
    border-bottom: 3px solid var(--green);
}
.table table{
    width: 100%;
    border-collapse: collapse;
}
.table thead{
    background-color: var(--green);
    color: white;
}
.table thead th{
    text-align: left;
}
.table tbody tr:not(:last-child){
    border: 1px solid rgb(191, 191, 191);
    border-left: none;
    border-right: none;
    color: rgb(13, 13, 13);
}
.table tbody tr:nth-child(even){
    background-color: rgb(239, 239, 239);
}.table tbody tr.danger{
    background-color: rgb(255, 162, 162);
    color: red;
    > td{
        padding: 20px;
        text-align: center;
        font-size: 1.4rem;
        font-weight: bold;
    }
}

.table tr td,
.table tr th{
    padding: 10px 8px;
}
.table tr td:last-child,
.table tr th:last-child{
    padding: 0px;
    width: 150px;
    text-align: center;
    border-left: 1px solid rgb(191, 191, 191);
}
.table tr td button{
    padding: 6px 10px;
    background-color: var(--green);
    color: white;
    font-size: 1.1rem;
    outline: none;
    border: none;
    cursor: pointer;
    transition: 0.4s all;
}
.table tr td button:last-child{
    margin-left: 10px;
}
.table tr td button:hover,
.table tr td button:focus{
    background-color: white;
    color: var(--green);
    outline: 2px solid var(--green)
}
